<template>
    <div class="wrap">
        <!--查询表单-->
        <el-form class="query-form" label-position="left" size="small" :inline="true" :model="formQuery" label-width='80px'>
            <el-form-item label="机会名称">
                <el-input v-model="formQuery.clue_name" clearable placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submit">查询</el-button>
            </el-form-item>
            <el-form-item>
                &nbsp;&nbsp;&nbsp;&nbsp;<a class="text-base" @click='query_show = !query_show; '>更多查询条件<i :class="'iconfont icon-'+query_show"></i></a>
            </el-form-item>
            <div v-show="query_show">
                <div class="input-box">
                    <el-form-item label="拥有者">
                        <el-input v-model="formQuery.clue_owner" clearable placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="公司名称">
                        <el-input v-model="formQuery.company_name" clearable placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="跟进状态">
                        <el-select v-model="formQuery.follow_type" clearable>
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </div>
                <div class="input-box">
                    <el-form-item label="手机号">
                        <el-input size="small" v-model="formQuery.phone" clearable placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="跟进时间">
                        <el-date-picker 
                            v-model="formQuery.recent_follow_time"
                            type="daterange"
                            style="width: 250px; margin-right: 30px;"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            value-format="yyyy-MM-dd"
                            :default-time="['00:00:00', '23:59:59']">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="分类">
                        <el-select v-model="formQuery.classify" clearable>
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </div>
                <div class="input-box">
                    <el-form-item label="机会阶段">
                        <el-select v-model="formQuery.classify" clearable>
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="机会类型">
                        <el-select v-model="formQuery.classify" clearable>
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </div>
            </div>
        </el-form>
        <!-- 操作按钮 -->
        <el-row>
            <el-button type="primary" size="small" @click="dialogAddVisible = true">新增</el-button>
            <el-button type="primary" size="small" @click="">导入</el-button>
            <el-button type="primary" size="small" @click="">删除</el-button>
            <el-button type="primary" size="small" @click="">转给他人</el-button>
        </el-row>
        <!--客户表格数据展示-->
        <el-table class="table" :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                max-height="500" border style="width: 100%; margin: 20px 0;"
                header-cell-class-name='text-center'
                cell-class-name='text-center' :fit='true'
                @selection-change="handleSelectionChange"
                :default-sort="{prop: '', order: 'descending'}">
            <el-table-column type="selection" width="40"></el-table-column>
            <el-table-column prop="company_name" label="机会名称"></el-table-column>
            <el-table-column prop="filed_must_own" sortable label="公司名称"></el-table-column>
            <el-table-column prop="filed_must_cus_type" min-width="100" sortable label="预计营销金额"></el-table-column>
            <el-table-column prop="filed_must_cus_size" label="机会拥有者"></el-table-column>
            <el-table-column prop="filed_must_industry" label="所属部门"></el-table-column>
            <el-table-column prop="filed_must_phone" label="机会阶段"></el-table-column>
            <el-table-column prop="filed_must_state" label="机会类型"></el-table-column>
            <el-table-column prop="filed_must_followTime" label="签单可能性"></el-table-column>
            <el-table-column prop="filed_must_followTime" label="最近跟进时间"></el-table-column>
            <el-table-column prop="filed_must_followTime" label="审批状态"></el-table-column>
            <el-table-column width="60" label="操作">
                <template slot-scope="scope">
                    <a class="text-base">编辑</a>
                </template>
            </el-table-column>
        </el-table>
        <!--分页-->
        <el-pagination v-if="tableData.length" background layout="total,prev, pager, next"
            :page-size="pagesize"
            @current-change="currentPage = val"
            :current-page="currentPage"
            :total="tableData.length" style="float: right;">
        </el-pagination>
        <!-- 新增机会模态框 -->
        <el-dialog title="新增机会" width="600px" class="dialogAdd" :visible.sync="dialogAddVisible" :close-on-click-modal='false'>
            <el-form ref="form" :model="formAdd" label-width="120px" size="small">
                <el-form-item label="机会名称：">
                    <el-input v-model="formAdd.name"></el-input>
                </el-form-item>
                <el-form-item label="公司名称：">
                    <el-select v-model="formAdd.origin">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="预计营销金额：">
                    <el-input v-model="formAdd.mail"></el-input>
                </el-form-item>
                <el-form-item label="营销阶段：">
                    <el-select v-model="formAdd.origin" placeholder="请选择">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="机会类型：">
                    <el-select v-model="formAdd.origin" placeholder="请选择">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="机会来源：">
                    <el-select v-model="formAdd.origin" placeholder="请选择">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="机会拥有者：">
                    <el-input v-model="formAdd.zipcode"></el-input>
                </el-form-item>
                <el-form-item label="所属部门：">
                    <el-select v-model="formAdd.origin" placeholder="请选择部门">
                    <el-option label="区域一" value="shanghai"></el-option>
                    <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="备注：">
                    <el-input type="textarea" v-model="formAdd.notes" :rows="4"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogAddVisible = false">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import { mapState, mapActions, mapMutations } from 'vuex'
    export default {
        data() {
            return {
                query_show: false,          //  是否显示更多查询
                currentPage: 1,//默认开始页面
                pagesize: 2,//每页的数据条数
                formQuery: {
                    clue_name: '',
                    clue_owner: '',
                    company_name: '',
                    follow_type: '',
                    phone: '',
                    recent_follow_time: [],
                    classify: ''
                },
                tableData: [{company_name: '3412'}],
                formAdd: {},     //  新增机会表单
                dialogAddVisible: false,  // 新增
                dialogImportVisible: false,  // 导入
                dialogDelVisible: false,  // 删除
                dialogTransferVisible: false,  // 转给他人
            }
        },
        methods: {
            /**
             * 搜索查询
             */
            submit(){
                console.log(this.formQuery);
            },
            
            /**
             * 表格勾选
             */
            handleSelectionChange(){

            },
        }
    }
</script>
<style lang="less" scoped>
    .query-form{
        .el-input, .el-select, .el-date-picker{
            width: 250px;
            margin-right: 30px;
        }
    }
    .dialogAdd{
        .el-input, .el-select, .el-textarea{
            width: 330px;
        }
    }
</style>




